.productCard__comments {
	width: 100%;
	padding-top: 40px;
	font: 400 16px/normal 'PT Sans';
	position: relative;
	color: #4f4f4f;
}

.productCard__commentConstructor {
	padding: 8px;
	width:380px;
	right:0;
	top:100%;
	position: absolute;
	background: #fff;
	box-sizing: border-box;
	display: none;
	box-shadow: 0 4px 64px 0 rgba(0,0,0,0.15);
	z-index: 30;
}
@media (max-width:396px) {
	.productCard__commentConstructor {
		width:304px;
		width:95vw;
	}
}
.productCard__commentConstructor-head {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 2.197%;
}
.productCard__commentConstructor-headText {
	font-size: 1.5em;
}
.productCard__commentConstructor-container {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	position: relative;
}
#showCommentConstructor__fafTQw,
#hideCommentConstructor__fafTQw  {
	display: none;
}
#showCommentConstructor__fafTQw:checked ~ * .productCard__commentConstructor {
	display: block;
}
.productCard__commentConstructor--sended {

}
.productCard__commentConstructor--sended .productCard__commentConstructor-starsContainer,
.productCard__commentConstructor--sended .productCard__commentConstructor-text,
.productCard__commentConstructor--sended .productCard__commentConstructor-container,
.productCard__commentConstructor--sended .productCard__commentConstructor-mail,
.productCard__commentConstructor--sended .productCard__commentConstructor-buttonSet {
	opacity: 0.35;
}
.productCard__comments-head {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 0 32px 8px;
}
.productCard__comments-headText {
	font-weight: bold;
}
.productCard__comments-openButton {
	color: #0054a6;
	cursor: pointer;
	&:hover {
		opacity:.8;
	}
}
.productCard__commentConstructor-photo {
	border-radius: 3px;
	text-align: center;
	background: rgba(0,84,166,.1);
	position:relative;
	width:45.604%;
	height:48px;
	margin: 2.197%;
	display: block;
	cursor: pointer;
}

.productCard__commentConstructor-photo input[type="file"] {
	display: none;
}
.productCard__commentConstructor-photoText {
	display: inline-block;
	position: absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	width:100%;
	height:1.2em;
	margin: auto;
	transition: transform .2s ease;
	text-shadow: 1px 0px 1px rgb(229, 238, 246),
								-1px 0px 1px rgb(229, 238, 246),
								0px -1px 1px rgb(229, 238, 246),
								0px 1px 1px rgb(229, 238, 246),
								1px 1px 1px rgb(229, 238, 246),
								-1px 1px 1px rgb(229, 238, 246),
								1px -1px 1px rgb(229, 238, 246),
								-1px -1px 1px rgb(229, 238, 246);
	& span {
		font-size: 12px;
		color: #bdbdbd;
		display: block;
	}
}

.productCard__commentConstructor-nameStarsContainer {
	padding: 2.197%;
	width: 45.604%;
}
.productCard__commentConstructor-name {
	text-align: center;
	position:relative;
}
.productCard__commentConstructor-mail  {
	padding: 2.197%;
}
.productCard__commentConstructor-name input,
.productCard__commentConstructor-mail input {
	font-size: 14px;
}
.productCard__commentConstructor-name [data-style="InputForText"],
.productCard__commentConstructor-mail [data-style="InputForText"] {
	border-color: #e0e0e0;
}
.productCard__commentConstructor-name [data-style="InputForText"] span,
.productCard__commentConstructor-mail [data-style="InputForText"] span{
	color: #0054a6;
}
.productCard__commentConstructor-starsContainer {
	text-align: center;
	position: relative;
	padding: 8px 0;
	font-size: 14px;
}
.productCard__commentConstructor-starsContainer > span{
	display: inline-block;
	vertical-align: middle;
}
[name="stars__daszcDD"] {
	display: none;
}
[name="stars__daszcDD"] + label {
	padding: 0 2px;
	display: inline-block;
	cursor: pointer;
	fill:#EFCE4A;
}
.productCard__commentConstructor-stars {
	display: inline-block;
	vertical-align: middle;
	cursor: default;
	font-size: 0;
	fill:inherit;
	margin: 7px 0;
}
.productCard__commentConstructor-stars label svg {
	width:1rem;
	height:1rem;
	padding: .05rem;
}

.productCard__commentConstructor-stars input[type="radio"]:checked + label ~ label {
	stroke: #e0e0e0;
	stroke-width: 3px;
	fill: none;
}
.productCard__commentConstructor-stars:hover label {
	fill:#EFCE4A !important;
	stroke-width: 0px!important;
}
.productCard__commentConstructor-stars label:hover ~ label {
	stroke: #e0e0e0!important;
	stroke-width: 3px!important;
	fill: none!important;
}
.productCard__commentConstructor-text {
	position: relative;
	padding: 2.197%;
	z-index: 1;
}
.productCard__commentConstructor-text [data-style="textarea"] {
	border-color: #e0e0e0;
}
.productCard__commentConstructor-text [data-style="textarea"] span{
	color: #0054a6;
}

.productCard__commentConstructor-line {
	height:1px;
	background: #f2f2f2;
	margin: 2.197%;
}

.productCard__commentConstructor-buttons {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.productCard__commentConstructor-buttonClose,
.productCard__commentConstructor-buttonSet {
	padding: 12px;
	width:45.604%;
	padding: 12px;
	margin: 2.197%;
	font-size: 14px;
	border: none;
	border-radius: 3px;
	-webkit-box-shadow: 0 0px 0px 0 rgba(0,0,0,0.1);
	box-shadow: 0 0px 0px 0 rgba(0,0,0,0.1);
	-webkit-transition: all .13s ease;
	transition: all .13s ease;
	position: relative;
  box-sizing: border-box;
  text-align: center;
	cursor: pointer;
}

.productCard__commentConstructor-buttonClose {
	background: #eee;
	color:#666;
}
.productCard__commentConstructor-buttonClose:hover {
	background: #e0e0e0;
	-webkit-transition: all .1s ease;
	transition: all .1s ease;
}
.productCard__commentConstructor-buttonClose:active {
	-webkit-transform: scale(.95);
	transform: scale(.95);
	color:inherit;
}
.productCard__commentConstructor-buttonSet {
	background: #0054a6;
	color:#fff;
}
.productCard__commentConstructor-buttonSet:hover {
	background: #004486;
	transition: all .1s ease;
}

.productCard__commentConstructor-buttonSet:active {
	transform: scale(.95);
	color:#fff;
}
.productCard__commentConstructor--sended .productCard__commentConstructor-buttonSet:active {
	transform: scale(1);
}
#icon-allPack__qDasfdfR {
	display: none;
}


.red--error {
	border-color: #ff0037;
}
.input--error {
	border:1px solid #ff0037 !important;
}


.loader,
.loader:before,
.loader:after {
	border-radius: 50px;
	border:3px solid #fff;
	border-bottom: 3px solid transparent;
	border-top: 3px solid transparent;
	box-sizing: border-box;
}


.loader {
	width:30px;
	height:30px;
	position: absolute;
	top:0;
	left:0;
	right:0;
	bottom: 0;
	margin:auto;
	display: block;
	background:transparent;
	transform-origin: 50% 50%;
	animation: rotation 2s linear infinite;
}
.loader:before {
	content:'';
	width:20px;
	height:20px;
	position: absolute;
	display: block;
	background:transparent;
	top:2px;
	left:2px;
	transform-origin: 50% 50%;
	animation: rotation 3s linear infinite;
}
.loader:after {
	content:'';
	width:10px;
	height:10px;
	position: absolute;
	display: block;
	background:transparent;
	top:6.5px;
	left:6.5px;
	transform-origin: 50% 50%;
	animation: rotation 6s linear infinite;
}
@keyframes rotation {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}




/* =============================*/

.productCard__comment {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}
.productCard__comment-photo {
	width:13.333%;
	height: 0;
	padding-bottom: 13.333%;
	margin:12px 3.75% 12px 1.6667%;
	border-radius: 60px;
	background: #eee;
	background-size: cover;
	background-repeat: no-repeat ;
	background-position: center center;
}
.productCard__comment-texts {
	width: 81.25%;
	font-size: 0.875em;
	margin: 12px 0 12px 2.0833%;
}
.productCard__comment-name {
	font-weight: bold;
	font-size: 16px;
}
.productCard__comment-stars {
	padding:0 16px;
	font-size: 0;
}
.productCard__comment-star {
	width:16px;
	height: 16px;
	display:inline-block;
	padding:0 2px;
}
.productCard__comment-star svg {
	fill:#e0e0e0;
	width:1rem;
	height:1rem;
}
.stars--5 .productCard__comment-star:nth-of-type(-n+5) svg,
.stars--4 .productCard__comment-star:nth-of-type(-n+4) svg,
.stars--3 .productCard__comment-star:nth-of-type(-n+3) svg,
.stars--2 .productCard__comment-star:nth-of-type(-n+2) svg,
.stars--1 .productCard__comment-star:nth-of-type(-n+1) svg {
	fill:#EFCE4A;
}

.productCard__comment-text {
	padding-top:16px;
}
.productCard__comments-pages {
	margin:10px;
}
.productCard__comments-pages ul {
	text-align: right;
}
.productCard__comments-pages ul li{
	text-align: center;
	display: inline-block;
	padding: 4px;
	width:20px;
	line-height: 20px;
	border-radius: 3px;
	cursor: pointer;
	&:hover {
		opacity:.7;
	}
}
.productCard__comments-pages ul li.active{
	color:#0054a6;
}

@media (min-width:1000px) {
	.productCard__comment-name,
	.productCard__comment-stars{
		display: inline-block;
	}

}

@media (max-width:1000px) {
	.productCard__comment-photo {
		margin-right: 10px;
	}
	.productCard__comment-stars {
		padding: 0;
	}
}
