.bigPrintsCarousel {
  padding: 10px;
  font:400 1em/normal 'PT Sans';
  max-width: 1200px;
  position: relative;
  margin: 0 auto;
}
.bigPrintsCarousel__head {
  color:#4f4f4f;
  padding: 10px 0;
  border-bottom: 1px solid #828282;
  display: flex;
  justify-content: space-between;
}
.bigPrintsCarousel__headName {
  font-size: 1.125em;
  padding-right: 20px;
}

.bigPrintsCarousel__headText a{
  font:inherit;
  font-size: 0.875em;
  color: #0054a6;
}
.bigPrintsCarousel__headText a:hover {
  opacity: .8;
}
.owl-item .bigPrintsCarousel__item {
  display: block;
}
.bigPrintsCarousel__item {
  display: none;
}

.bigPrintsCarousel__item > * {
  margin: 5px 7px 30px;
}
.catalogCard__hoverContent-button:before {
  display: none !important;
}
.bigPrintsCarousel-carousel {

  & .owl-pre {

  }
  & .owl-prev,
  & .owl-next {
    width:12px;
    height: 12px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  & .owl-prev {
    left: 0;
  }
  & .owl-next {
    right: 0;
  }
}
.owl-prev,
.owl-next {
  cursor: pointer;
}
.bigPrintsCarousel-carousel .owl-stage-outer {
  overflow: hidden;
}
.bigPrintsCarousel-carousel .owl-stage {
  display: flex;
  justify-content: flex-start;
  margin: 0 auto;
}
.bigPrintsCarousel-carousel .owl-prev,
.bigPrintsCarousel-carousel .owl-next {
  & svg {
    fill: #828282;
    width:inherit;
    height:inherit;
    box-sizing: border-box;
  }
}
.bigPrintsCarousel-carousel .owl-prev {
  & svg {
    transform: rotate(180deg);
  }
}
